body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  box-sizing: border-box;
  font-size: 20px;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body, h1, h2, h3, h4, h5, h6, p, ol, ul {
  margin: 0;
  padding: 0;
  font-weight: normal;
}

ol, ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  display: -webkit-flex;
  display: flex;
  min-height: 100vh;
  -webkit-flex-direction: column;
          flex-direction: column;
}
#root {
  -webkit-flex: 1 1;
          flex: 1 1;
}
footer {
  text-align: center;
  margin: 5px 10px;
}
footer a, footer a:visited {
  color: #222;
}

.Game {
    text-align: center;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.Game .wrapper {
    padding: 2vmin;
    background-color: #ccc;
    margin-bottom: 2vmin;
}
.Game .bestScore {
    margin: 2vmin 0;
}

.Game .difficulty-level {
    background-color: #eee;
    color: #333;
    font-weight: bold;
    font-size: 3vmin;
    outline: none;
    margin: 0 1vmin;
}
.Game .difficulty-level.selected {
    background-color: #333;
    color: #eee;
    font-weight: bold;
}

.Square {
    width: 7vmin;
    height: 7vmin;
    font-size: 5vmin;
    font-weight: bold;
    line-height: 5vmin;
    background: #eee;
    border: 1px outset #ddd;
    cursor: default;
    display: inline-block;
    outline: none;
}
.Square.unrevealed:active {
    background: #999;
    border: 1px inset #fff;
}
.Square.emoji {
    font-size: 3vmin;
}
.Square.revealed, .Square.mine {
    background: #ddd;
}
.Square.mine.clicked {
    background-color: #a00;
}
.Square.number1 {
    color: blue;
}
.Square.number2 {
    color: green;
}
.Square.number3 {
    color: red;
}
.Square.number4 {
    color: darkblue;
}
.Square.number5 {
    color: darkred;
}
.Square.number6 {
    color: teal;
}
.Square.number7 {
    color: #333;
}
.Square.number8 {
    color: deeppink;
}

.Board {
    display: inline-grid;
    grid-gap: 0;
    grid-column-gap: 0;
    -webkit-column-gap: 0;
            column-gap: 0;
    margin: 0 auto;
    border: 1vmin solid #ddd;
    overflow: auto;
    max-width: 100%;
}
.Board.disabled {
    pointer-events: none;
}

.status {
    display: inline-block;
    margin: 0 auto;
}
.status::after {
    clear: both;
    content: "";
    display: block;
}
.lcd {
    display: inline-block;
    margin: 0 4vmin;
    font-family: 'Orbitron', monospace, sans-serif;
    color: #f00;
    background: #333;
    font-size: 5vmin;
    float: left;
    width: 16vmin;
    height: 8vmin;
    line-height: 9vmin;
}
.restart {
    text-align: center;
    background: #fff;
    font-size: 4vmin;
    margin: 0 4vmin;
    float: left;
    min-width: 8vmin;
    min-height: 8vmin;
    line-height: 6vmin;
}

